import { format } from '../utils/time';

export function formatCash(value = 0) {
    const num = Number(value);

    if (num % 1 !== 0) return num;

    return parseFloat(value / 100).toFixed(2);
}

export function formatNumber(value = 0) {
    return value.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
}

export function formatLargeNumber(value = 0) {
    const limit = 100000;
    const divider = 10000;
    let res;

    if (value > limit) {
        res = (value / divider).toFixed(2).slice(0, -1);
        res = `${res}万`;
    } else {
        res = value.toLocaleString();
    }

    return res;
}

export function formatDate(timestamp = 0) {
    return new Date(timestamp).toLocaleString('zh', { hour12: false });
}

export function formatTimeAgo(timestamp, limit = 'week') {
    const array = ['minute', 'hour', 'day', 'week', 'month', 'year'];
    const milliseconds = [
        1000, // 1 * 1000
        60000, // 60 * 1000,
        3600000, // 60 * 60 * 1000,
        86400000, // 24 * 60 * 60 * 1000,
        604800000, // 7 * 24 * 60 * 60 * 1000,
        2592000000, // 30 * 24 * 60 * 60 * 1000,
        31104000000, // 12 * 30 * 24 * 60 * 60 * 1000,
    ];
    const text = ['秒前', '分钟前', '小时前', '天前', '周前', '月前', '年前'];
    const durationMilliseconds = Date.now() - timestamp;

    // 职责链模式
    for (let i = 0, len = milliseconds.length; i < len; i += 1) {
        if (i + 1 === len || durationMilliseconds <= milliseconds[i + 1]) {
            const duration = Math.floor(durationMilliseconds / milliseconds[i]);

            if (duration === 0) {
                return '刚刚';
            }
            return duration + text[i];
        }

        if (limit === array[i]) {
            return formatDate(timestamp);
        }
    }

    return '刚刚';
}

export function formatDay(timestamp = 0) {
    const currentDay = new Date().getDate();
    const date = new Date(timestamp);
    const day = date.getDate();
    const time = [date.getHours(), date.getMinutes(), date.getSeconds()]
        .map(t => (t < 10 ? `0${t}` : t))
        .join(':');

    const diff = currentDay - day;

    switch (diff) {
    case 0:
        return `今天 ${time}`;
    case 1:
        return `昨天 ${time}`;
    case 2:
        return `前天 ${time}`;
    default:
        return format(date);
    }
}
