<template>
    <div class="income-list">
        <div
            class="income-list__item Hairline Hairline_bottom"
            v-for="item in incomeList"
            :key="item.recordTime">
            <div class="income-list__info">
                <div class="income-list__reason">{{ item.reason }}</div>
                <div class="income-list__date">{{ item.recordTime | formatDay }}</div>
            </div>
            <div
                :class="[
                    'income-list__coin',
                    { positive: item.change > 0 },
                    { negetive: item.change <= 0 },
            ]">
                <span>{{ formatCoin(item.change) }}</span>金币
            </div>
        </div>
    </div>
</template>

<script>
import { formatNumber, formatDay } from '@/filters';

export default {
    name: 'IncomeList',
    props: {
        incomeList: {
            type: Array,
            default: () => ([]),
        },
    },
    filters: {
        formatDay,
    },
    methods: {
        formatCoin(value) {
            if (value > 0) {
                return `+${formatNumber(value)}`;
            }

            return formatNumber(value);
        },
    },
};
</script>

